@media only screen and (max-width: 767px) {
    h1 {
        font-size: 19px;
    }
    .section-slide-show {     
        
        .banner-wrapper {
            
            height: 200px !important;
    
            img {
                height: 130px !important;
                left: -7% !important;
            }
    
            .center {
                top: 40% !important;
                // left: 0% !important;
                h1 {
                    font-size: 20px !important;
                    line-height: 30px !important;
                }
            }
        }
    }     
}

