* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: 'Varela Round', sans-serif;
  font-size: 15px;
  direction: rtl;
}

section {
	clear: both;
	padding: 0px;
	margin: 0px;
}

a {
  display: block;
  text-decoration: none;
  color: #2d2d2d;
  outline: 0;
  box-shadow: unset;
  transition: 0.5s;
}

a:hover, a:focus {
  text-decoration: none;
  color: #8FD3E8;
}

h1 {
  color: #8FD3E8;
  margin-bottom: 15px;
  font-size: 33px;
}

 .h2, .h3, .h4, .h5, .h6, h2, h3, h4, h5, h6 {
  color: #000000;
  margin-bottom: 15px;
}

.h1:last-child, .h2:last-child, .h3:last-child, .h4:last-child, .h5:last-child, .h6:last-child, h1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child {
  margin-bottom: 0;
}

p {
  font-size: 15px;
  color: #646464;
  line-height: 1.8;
  margin-bottom: 15px;
}

p:last-child {
  margin-bottom: 0;
}

button {
  font-weight: 500;
  border: none;
  padding: 8px 30px;
  font-size: 13px;
  color: white;
  background: #8FD3E8;
  cursor: pointer;
  transition: 0.5s;
  -webkit-transition: 0.5s;
}

button:hover {
  background: rgb(98, 155, 173);
}

@media only screen and (max-width: 767px){h1{font-size:19px}.section-slide-show .banner-wrapper{height:200px !important}.section-slide-show .banner-wrapper img{height:130px !important;left:-7% !important}.section-slide-show .banner-wrapper .center{top:40% !important}.section-slide-show .banner-wrapper .center h1{font-size:20px !important;line-height:30px !important}}

.section-slide-show{width:100%}.section-slide-show .banner-wrapper{position:relative;height:530px;width:100%}.section-slide-show .banner-wrapper img{position:absolute;height:400px;width:auto;margin:0;top:50%;left:2%;transform:translateY(-50%)}.section-slide-show .banner-wrapper .center{margin:0;position:absolute;top:50%;left:60%;transform:translate(-50%, -50%)}.section-slide-show .banner-wrapper .center h1{font-size:50px;line-height:80px;color:white}.section-slide-show .banner-wrapper1{position:relative;height:530px;width:100%;background:red}

.feature-card{margin:0px 50px auto;display:flex;flex-direction:column;align-items:center;text-align:center}.feature-card .card-icon{margin-bottom:20px}.feature-card .card-icon img{height:60px;width:auto}.feature-card .card-content{margin-bottom:15px;width:75%}.feature-card .card-content h2{color:#8FD3E8;margin-bottom:10px;margin-top:0}.feature-card .card-content p{margin:0;font-size:13px}

.section-features{width:100%}.section-features .features-header{margin-bottom:70px}.section-features .features-header h1{margin-top:70px}.section-features .features-header p{font-size:17px}.section-features .features-content{width:75%;margin:0 auto;display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));grid-template-rows:auto;grid-row-gap:50px;row-gap:50px;margin-bottom:50px}


.image-banner-wrapper{width:100%;height:500px;background-size:cover;background-repeat:no-repeat;background-position:50% 50%;background-attachment:fixed}

