.header-container{width:100%;position:relative;padding-top:15px}.header-container .login-user-link{position:absolute;top:25px;right:80px;display:flex;align-items:center;cursor:pointer}.header-container .login-user-link span{font-size:13px;display:inline;color:#8FD3E8;margin-right:10px}.header-container .login-user-link img{height:23px;width:auto}.header-container .top-wrapper{width:100%;display:flex;flex-direction:column;align-items:center;margin-bottom:20px}.header-container .top-wrapper img{width:50px;height:50px}.header-container .top-wrapper h1{color:#8FD3E8}.header-container .botom-wrapper{width:100%;background:#f7f7f7}.header-container .botom-wrapper nav{width:55%;margin:0 auto}.header-container .botom-wrapper nav .nav-content{text-align:center}.header-container .botom-wrapper nav .nav-content ul{list-style:none;margin:0;padding:0;display:flex;flex-direction:row;align-items:center;justify-content:space-evenly;font-size:13px}.header-container .botom-wrapper nav .nav-content ul li{position:relative;display:inline-block;width:25%;padding:10px 0}.header-container .botom-wrapper nav .nav-content ul li .dropdown-content{display:none;top:36px;position:absolute;background-color:rgba(247,247,247,0.5);min-width:100%;box-shadow:0px 8px 16px 0px rgba(0,0,0,0.2);z-index:1}.header-container .botom-wrapper nav .nav-content ul li .dropdown-content .link{padding:8px 0}.header-container .botom-wrapper nav .nav-content ul li:hover .dropdown-content{display:block}.header-container .botom-wrapper nav .nav-content ul li p{margin:0}

.footer-container{width:100%;background:#8FD3E8;height:140px;display:flex;align-items:center}.footer-container .copyrigth-area{display:flex;flex-direction:column;justify-content:flex-start;text-align:start;padding-right:25%}.footer-container .copyrigth-area p{color:white;font-size:12px;margin-bottom:5px}.footer-container .copyrigth-area p span{font-size:15px}.footer-container .copyrigth-area .social-media-area a img{height:17px;width:auto}

.loader .ring-2{position:relative;width:45px;height:45px;margin:0 auto;border:4px solid #4b9cdb;border-radius:100%}.loader .ring-2 .ball-holder{-webkit-animation:loadingE 1.3s linear infinite;animation:loadingE 1.3s linear infinite}.loader .ring-2 .ball-holder{position:absolute;width:12px;height:45px;left:17px;top:0px}.loader .ring-2 .ball-holder .ball{position:absolute;top:-11px;left:0;width:16px;height:16px;border-radius:100%;background:#4282B3}@-webkit-keyframes loadingE{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes loadingE{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}


