.loader {
    
    .ring-2 {
        position: relative;
        width: 45px;
        height: 45px;
        margin: 0 auto;
        border: 4px solid #4b9cdb;
        border-radius: 100%;
        
        .ball-holder {animation: loadingE 1.3s linear infinite;}

        .ball-holder {
            position: absolute;
            width: 12px;
            height: 45px;
            left: 17px;
            top: 0px;

            .ball {
                position: absolute;
                top: -11px;
                left: 0;
                width: 16px;
                height: 16px;
                border-radius: 100%;
                background: #4282B3;
              }
        }
    }
    
}

@keyframes loadingE {
  0% {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}
